:root {
  --active-text-color: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
}

.logo-header {
  line-height: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  max-width: 100%;
  padding: 0 50px;
  display: flex;
}

.navigation-list {
  margin: 0;
  padding: 0;
  display: flex;
}

.navigation-item {
  list-style: none;
  display: flex;
  position: relative;
}

.navigation-item:not(:last-child) {
  margin-right: 20px;
}

.navigation-link {
  color: #000;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
}

.navigation-link:hover, .navigation-link.is-active {
  color: var(--active-text-color);
}

.navigation-link.is-active:after {
  content: "";
  background-color: var(--active-text-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}
